.education_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
    
}

.education {
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.education-special{
    height: 23.5rem;
}

.education:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.education_head {
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.education_head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.education_list {
    padding: 2rem;
}

.education_list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8;
}

.education_list-icon {
    color: var(--color-primary);
    margin-top: 20px;
    flex-shrink: 0;
}

.education_list p {
    margin-top: 1rem;
    font-size: 0.9rem;
}

/* ****** MEDİUM DEVİCES ****** */

@media screen and (max-width:1024px) {
    .education_container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }

    .education {
        height: auto;
    }
    .education-special{
        height: auto;
    }

}


/* ****** SMALL DEVİCES ****** */

@media screen and (max-width:600px) {

    .education_container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
        place-items: center;
    }

    .education {
        height: auto;
        width: 85%;
    }


}