.about-container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}



.about-me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,
            transparent,
            var(--color-primary),
            transparent);
    display: grid;
    place-items: center;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.about-me-image {
    position: relative;
    width: 100%;
    padding-top: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
   
    
}

.about-me-image img{
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    filter: blur(1px);
    

}



.about-me-image:hover {
    transform: rotate(0deg);
}

.about-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about-card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}


.about-card:hover {
    background: transparent;
    background-color: var(--color-primary-variant);
    cursor: default;

}

.about-icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about-card h5 {
    font-size: 0.95rem;

}

.about-card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about-content p {
    text-align: justify;
    margin: 2rem 0 2rem;
    color: var(--color-light);
}

.about-content .detail {
  margin-top: -0.75rem;
}



/* ****** MEDİUM DEVİCES ****** */

@media screen and (max-width:1024px) {
    .about-container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about-me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about-content p {
        margin: 1rem 0 1.5rem;
    }


}


/* ****** SMALL DEVİCES ****** */

@media screen and (max-width:600px) {
    .about-me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about-cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem
    }

    .about-content {
        text-align: center;
    }

    .about-content p {
        margin: 1.5rem 0;
    }



}